import React from 'react';
import {Link} from 'react-router-dom';
import {Card, CardBody, Button, Form, FormGroup, Label, Input, CardTitle} from 'reactstrap';

const HomeComponent = () => 
{
    return(
        <div className="container">
            <img id="rotate" src='/assets/welcome.png' alt="logo"/>
            <Card className="profile">
                <CardTitle> Give an Attempt for the Quiz on General Abbreviations! </CardTitle>
                <CardBody>
                    <Form >
                        <FormGroup className = "row">
                            <Label htmlFor="name"> Your Name: </Label>
                            <Input name="name" placeholder="name"/>
                        </FormGroup>
                    </Form>
                    <Button type="submit" color="warning"> <Link to="/quiz"> START QUIZ </Link> </Button>
                </CardBody>
            </Card>
        </div>
    );

}

export default HomeComponent;